/*--------------------search-results-page----------------------*/
.page-search-resuls .title-with-dd {
  padding-top: 40px;
}

.title-with-dd {
  padding-top: 40px;
  font-size: 42px;
  margin-bottom: 45px;
  font-size: 42px;
  line-height: calc(50 / 42);
  font-weight: 500;
}

@media (max-width: 1170px) {
  .title-with-dd {
    font-size: calc(31px + 11 * ((100vw - 320px) / 850));
  }
}

@media (max-width: 1170px) {
  .title-with-dd {
    margin-bottom: calc(31px + 14 * ((100vw - 320px) / 850));
  }
}

.title-with-dd span {
  margin-right: 16px;
}

.title-with-dd a {
  position: relative;
  color: #FC6733;
  border-bottom: 1px dashed #FC6733;
}

.filter-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  padding-bottom: 24px;
  border-bottom: 1px solid rgba(34, 34, 34, 0.05);
}

.filter-tags:not(:last-child) {
