}

.filter-tag {
  display: flex;
  align-items: center;
  height: 40px;
  padding: 10px 15.44px;
  background: #FFFFFF;
  border-radius: 20px;
}

.filter-tag.active {
  background-color: #FC6733;
  color: #fff;
}

.filter-tag.active .icon-close {
