.item-results-filter__dd .noUi-pips {
  top: -14px;
}

.item-results-filter__dd .noUi-pips div:first-of-type {
  display: none;
}

.item-results-filter__dd .noUi-pips div:last-of-type {
  display: none;
}

.item-results-filter__dd .noUi-pips div:nth-child(2), .item-results-filter__dd .noUi-pips div:nth-child(13) {
  display: none;
}

.item-results-filter__dd .noUi-pips div:nth-child(4), .item-results-filter__dd .noUi-pips div:nth-child(12) {
